import { Routes, Route, Link } from 'react-router-dom';
import React, { lazy, Suspense, createContext, useEffect } from "react";
import { Container, Button, Alert, Row, Col, Nav, Navbar } from 'react-bootstrap';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';
import data from './data';
import mainBg from './images/bg.png';
import './App.css';
import About from './components/About';
import Event from './components/Event';

const Detail = lazy(()=> import('./components/Detail.js'));
const Cart = lazy(()=> import('./components/Cart.js'));
const Test = lazy(()=> import('./Test.js'));

export let Context1 = createContext();

function App() {

  useEffect(()=>{
    let add = localStorage.getItem('watched');
    if(add == null){
      localStorage.setItem('watched', JSON.stringify([]));
    }
  }, [])

  let navigate = useNavigate();

  let result = useQuery('작명', ()=>
    axios.get('https://codingapple1.github.io/userdata.json').then((a)=>{
      console.log('요청됨');
      return a.data;
    }),
    { staleTime : 2000 }
  )

  let [shoes, setShoes] = useState(data);
  let [count, setCount] = useState(0);
  let [show, setShow] = useState(true);
  let [show2, setShow2] = useState(false);
  let [ssort, setSsort] = useState();
  let [inven] = useState([10, 11, 12]);
  
  let nameSort =
    (a, b) => a.title.toLowerCase() < b.title.toLowerCase() ? -1
    : a.title.toLowerCase() > b.title.toLowerCase() ? 1 : 0;
  
  function nameS(){
    setSsort(data.sort(nameSort));
  }

  return (
    <div className="App">

    <Navbar bg="light" variant="light">
      <Container>
        <Navbar.Brand href="/">Sshop</Navbar.Brand>
        <Nav className="me-auto">
          <Nav.Link onClick={()=>{ navigate('/')}}>메인</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/detail/0')}}>상세페이지</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/cart')}}>장바구니</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/event')}}>이벤트</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/about')}}>About</Nav.Link>
        </Nav>
        <Nav className="ms-auto">
          { result.isLoading && '로딩중' }
          { result.error && '에러' }
          { result.data && result.data.name }
        </Nav>
      </Container>
    </Navbar>
      <Suspense fallback={<div>로딩중</div>}>
      <Routes>
        <Route path="/" element={
            <div>
              <div className="main-bg" style={{backgroundImage: 'url('+ mainBg + ')'}}></div>
              <Button variant="primary" onClick={nameS}>정렬</Button>
              {show2 ? <div>로딩중</div> : null}
              <Container>
                <Row>
                  {
                    shoes.map(function(a, i){
                      return(
                        <Product shoes={shoes[i]} i={i} key={i}></Product>
                      )
                    })
                  }
                </Row>
              </Container>
              {show ? <button onClick={()=>{
                setShow2(true);
                Promise.all([axios.get('https://codingapple1.github.io/shop/data2.json'),
                axios.get('https://codingapple1.github.io/shop/data3.json')])
                .then((result)=>{
                  setCount(count+1);
                  console.log(count);
                  setShow2(false);
                  setShoes(shoes.concat(result[0].data));
                  if(count == 1){
                    setShoes(shoes.concat(result[1].data));
                    setShow(false);
                  }
                })
                .catch(()=>{
                  console.log('데이터를 불러오는데 실패했습니다.');
                  setShow2(false);
                })

                axios.post('')
              }}>더보기</button> : null }
            </div>} />
        <Route path="/detail" element={
          <Context1.Provider value={{ inven, shoes }}>
            <Detail shoes={shoes}/>
          </Context1.Provider>
        }>
          <Route path=":id" element={<Detail/>}/>
        </Route>
        <Route path="/test" element={<Test/>} />
        <Route path="/cart" element={<Cart/>} />
        <Route path="/about" element={<About />}>
          <Route path="member" element={<div>회사 멤버</div>} />
          <Route path="location" element={<div>회사 위치</div>} />
        </Route>
        <Route path="/event" element={<Event />}>
          <Route path="one" element={<div>첫 주문시 양배추즙 서비스</div>} />
          <Route path="two" element={<div>생일기념 쿠폰받기</div>} />
        </Route>
        <Route path="*" element={<div>404Error</div>}/>
      </Routes>
      </Suspense>
    </div>
  );
}

function Product(props){
  return(
    <Col sm={4}>
      <Link to={'detail/' + props.i}><img src={'https://codingapple1.github.io/shop/shoes' + (props.i+1) + '.jpg'}
      width="80%"/></Link>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{props.shoes.price}원</p>
    </Col>
  )
}

export default App;
