import { createSlice } from '@reduxjs/toolkit'

let user = createSlice({
  name: 'user',
  initialState: { name: 'kim', age: 20 },
  reducers: {
    changeName(state){
      state.name = 'park';
      // return { name: 'kim', age: 20 };
    },
    plusAge(state){
      state.age += 1;
      console.log(state.age);
    }
  }
})

export default user;

export let { changeName, plusAge } = user.actions;